import {React,useState} from "react";
import "./UploadBook.css";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

function UploadBook(props)
{ 
   const [validated, setValidated] = useState(false);

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
      console.log(form)
    }
    event.preventDefault();
    event.stopPropagation();
     console.log(form)

    setValidated(true);
  };
 
  return(  
            <Form noValidate validated={validated} onSubmit={handleSubmit} className="container form_container">
              <Form.Group controlId="formBasicName">
                  <Form.Label>Book Name</Form.Label>
                  <Form.Control type="text" placeholder="Enter Book Name" required/>              
              </Form.Group>

              <Form.Group controlId="formBasicEmail">
                  <Form.Label>Author</Form.Label>
                  <Form.Control type="text" placeholder="Enter Author" required/>              
              </Form.Group>

              <Form.Group controlId="exampleForm.ControlSelect1" >
                    <Form.Label>Book Condition</Form.Label>
                    <Form.Control as="select">
                      <option>Good</option>
                      <option>Average</option>
                      <option>Bad</option>                      
                    </Form.Control>
              </Form.Group>   

              <Form.Group controlId="exampleForm.ControlSelect1" >
                    <Form.Label>Book Category</Form.Label>
                    <Form.Control as="select">
                      <option>CSE</option>
                      <option>MECH</option>
                      <option>EEE</option>   
                      <option>ECE</option>                      
                      <option>CIVIL</option>                                            
                    </Form.Control>
              </Form.Group>              

              <Form.Group controlId="formBasicPassword">
                <Form.Label>Actual Price </Form.Label>
                <Form.Control type="number" placeholder="Enter Actual Price" required/>
              </Form.Group>

               <Form.Group controlId="formBasicPhoneno">
                <Form.Label>Selling Price</Form.Label>
                <Form.Control type="number" placeholder="Selling Price" required/>
              </Form.Group>  

              <Form.Group controlId="exampleForm.ControlTextarea1">
                  <Form.Label>Description</Form.Label>
                  <Form.Control as="textarea" rows={5} />
              </Form.Group>

              <Form.Group controlId="formBasicPhoneno">                                 
                <Form.File id="exampleFormControlFile1" label="Upload the cover photo" required/>
              </Form.Group>              

              <Button variant="success" type="submit">
                submit
              </Button>
          
          </Form>
          )
}

export default UploadBook;